<template>
  <div class="main">
    <div class="main_content">
      <slot></slot>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Main',
  components: {},
  data() {
    return {
      clientWidth: 0,
      mainRightMax: {
        width: '65%',
        maxWidth: '800px',
      },
      mainLeftMax: {
        width: '35%',
        maxWidth: '350px',
      },

      mainRightMin: {
        width: '100%',
      },
      mainLeftMin: {
        width: '100%',
      },
      mainRightStyle: this.mainRightMax,
      mainLeftStyle: this.mainLeftMax,
    };
  },
  created() {
    window.addEventListener('resize', this.handleResize, true);
    this.handleResize();
  },
  methods: {
    handleResize() {
      const {
        clientWidth,
      } = document.body;
      if (clientWidth > 820) {
        this.mainRightStyle = this.mainRightMax;
        this.mainLeftStyle = this.mainLeftMax;
      } else {
        this.mainRightStyle = this.mainRightMin;
        this.mainLeftStyle = this.mainLeftMin;
      }
    },
  },
};
</script>

<style>
  .main {
    margin: 0;
    width: 100%;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .main_content {
    width: 100%;
    max-width: 65rem;
    background-color: #FFFFFF;
    box-shadow: 0 0px 0 0 rgba(0,0,0,0.1), 0 2px 4px 0 rgba(0,0,0,0.1);
  }

  a:link {
    text-decoration: none;
  }

  a:active {
    text-decoration: none
  }

  a:hover {
    color: red;
  }

  a:visited {
    text-decoration: none;
  }
</style>
