<template>
  <div class="home">
    <div class="home_content">
      <Xpjjfafl></Xpjjfafl>
    </div>
    <div class="home_content">
      <div class="home_content_two" :style="homeContentTwoWidth">
        <Tsdx></Tsdx>
        </div>
      <div class="home_content_two" :style="homeContentTwoWidth">
        <Xqfx></Xqfx>
        </div>
    </div>

  </div>
</template>

<script>
import Xpjjfafl from '../home/Xpjjfafl.vue';
import Tsdx from '../home/Tsdx.vue';
import Xqfx from '../home/Xqfx.vue';


export default {
  name: 'Home',
  components: {
    Xpjjfafl,
    Tsdx,
    Xqfx,
  },
  data() {
    return {
      clientWidth: 0,
      homeContentTwoMax: {
        width: '50%',
      },
      homeContentTwoMin: {
        width: '100%',
      },
      homeContentTwoWidth: this.homeContentTwoMax,
    };
  },
  created() {
    window.addEventListener('resize', this.handleResize, true);
    this.handleResize();
  },
  methods: {
    handleResize() {
      const { clientWidth } = document.body;
      if (clientWidth > 820) {
        this.homeContentTwoWidth = this.homeContentTwoMax;
      } else {
        this.homeContentTwoWidth = this.homeContentTwoMin;
      }
    },
  },
};
</script>

