    <div class="top_info">
      <div class="top_info_body">
        <div class="top_info_sitename">夏普复合机解决方案网站</div>
        <div class="top_info_user">登录|注册|帮助</div>
      </div>
    </div>
    <div class="logo">
      <div class="log_body">
        <img src="../../assets/logo.png" />
      </div>
    </div>
    <div class="top_head" :style="justifyContent">
      <div class="top_nav">
        <a class="nav_btn top_elem" href="#">首页</a>
        <a class="nav_btn top_elem" href="#">夏普解决方案分类</a>
        <a class="nav_btn top_elem" href="#">他社动向</a>
        <a class="nav_btn top_elem" href="#">需求分享</a>
      </div>
      <div class="top_search top_elem">
        <form action="#">
          <input type="text" name="firstname"><input type="submit" value="搜索">
        </form>
      </div>
    </div>
    <transition name="top_bar_trans">
      <div class="top_bar" v-show="show_top_bar" @scroll="handleScroll">这里是状态栏</div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Top',
  data() {
    return {
      show_top_bar: false,
      justifyContentMax: {
        justifyContent: 'space-between',
      },
      justifyContentMin: {
        justifyContent: 'center',
      },
      justifyContent: this.justifyContentMax,
    };
  },
  created() {
    window.addEventListener('scroll', this.handleScroll, true);
    window.addEventListener('resize', this.handleResize, true);
    this.handleResize();
  },
  methods: {
    handleScroll() {
      const {
        scrollTop,
      } = document.documentElement;
      const {
        availHeight,
      } = window.screen;
      this.show_top_bar = scrollTop > availHeight * 0.5;
      this.show_top_bar = false;
    },
    handleResize() {
      const {
        clientWidth,
      } = document.body;
      if (clientWidth > 800) {
        this.justifyContent = this.justifyContentMax;
      } else {
        this.justifyContent = this.justifyContentMin;
      }
    },
  },
};
</script>

